header{
    text-align: left;
    position: relative;
    z-index: 10;
    @include desktop{
        position: relative;
        z-index: 10;
        margin-bottom: -62px;
        text-align: center;
    }
    .container{
         display: inline-flex;
         flex-wrap: wrap;
         align-items: center;
         justify-content: space-between;

         @include desktop{
             justify-content: center;
         }
            
             .header-top{
                display:inline-flex;
                flex-wrap: wrap;
                width: 100%;
                justify-content: center;
                padding: 16px 0px;
                
                .social{
                    width: 100%;
                    text-align: center;
                    margin-bottom: 10px;
                    a{
                        margin-right: 15px;
                        font-size: 17px;
                        transition: .30s;
                        &:last-child{
                            margin-right: 0px;
                        }

                        &:hover{
                            color: blue;
                        }
                    }
                }
                .contatos{
                    width: 100%;
                    text-align: center;
                    a{
                        margin: 0 0px 10px 0;
                        font-size: 15px;
                        width: 100%;
                        display: block;
                        transition: .30s;
                        &:last-child{
                            margin: 0px;
                        } 
                        
                        &:hover{
                            color: blue;
                        }   
                    }

                }

                @include tablet{
                    justify-content: space-between;
                    .social{
                        width: auto;
                        margin: 0px;
                    }
                    .contatos{
                        width: auto;
                        display: inline-flex;
                        a{
                            width: auto;
                            margin: 0 15px 0 0;
                            &:last-child{
                                margin:0px;
                            }

                        }
                    }
                }
             }
             .header-logo{
                img{
                display: block;
                margin: 0 auto;
                max-width: 100%;
               }
             }

             .header-bottom{
                display: inline-flex;
                width: auto;
                flex-direction: row-reverse;
                justify-content: space-between;

                .bar{
                    font-size: 35px;
                    z-index: 1;
                    cursor: pointer;

                    &:hover{
                        svg{
                            color: $color_destaque_hover;
                        }
                    }
                    svg{
                        transition: .30s;
                    }
                }

                .menu{
                    width: 100%;
                    height: calc(100vh  - 110px);
                    position: absolute;
                    top: 110px;
                    left: -100%;
                    transition: .30s;
                    background-color:$color_destaque;
                    overflow: auto;
                    padding: 20px 25px;
                    &.active{
                        left:0;
                        
                }
                ul{
                    li{
                        margin-bottom: 10px;
                        cursor: pointer;

                        a{
                            transition: .20s;
                            font-size: 16px;
                            font-family:'Montserrat Bold';

                        }
                        &:hover{
                            a{
                                color: $color_destaque_hover;
                            }
                        }
                    }
                
                }

                @include tablet{
                    top: 50px;
                }
            }

            @include desktop{
                width: 100%;
                justify-content: center;
                margin-top: 20px;
                .bar{
                    display: none;
                }
                .menu{
                    top:0;
                    left: 0;
                    position: relative;
                    height: auto;
                    padding: 20px 0px;
                    max-width: 970px;
                    overflow: initial;
                    ul{
                        width: 100%;
                        display: inline-flex;
                        justify-content: space-between;

                        li{
                            margin-bottom: 0px;
                        }
                    }

                    &:before{
                        content: ' ';
                        width: 75px;
                        height: 100%;
                        position: absolute;
                        left: -75px;
                        top: 0px;
                        background: rgb(7,24,153);
                        background: linear-gradient(225deg, rgba(7,24,153,1) 50%, rgba(255,255,255,0) 50%);
                    }
                    &:after{
                        content: ' ';
                        width: 75px;
                        height: 100%;
                        position: absolute;
                        right: -75px;
                        top: 0px;
                        background: rgb(7,24,153);
                        background: linear-gradient(125deg, rgba(7,24,153,1) 50%, rgba(255,255,255,0) 50%);
                }   }
            }
        }
    }
}

body.admin{
    background-color: #fff;
}


body.admin header{
    position: relative;
    background-color: #000;

    .container{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 10px;
        padding-bottom: 10px;
        .left{
            svg{
                font-size: 35px;
                cursor: pointer;
                transition: .30s;
                &:hover{
                    background-color: $color_destaque;
                }

                @include desktop{
                    display: none;
                }
            }
        }
        .right{
            .menu{
                ul{
                    li{
                        position: relative;
                        display: inline-flex;
                        align-items: center;
                        cursor:pointer;
                        svg{
                            font-size: 14px;
                            margin: 0 5px;
                        }

                        img{
                            width: 50px;
                            height: 50px;
                            border-radius: 50%;
                            overflow: hidden;
                            border: 2px solid #fff;
                        }

                        .dropdown{
                            position: absolute;
                            top: 100%;
                            left: 0;
                            padding: 15px 0px;
                            display: inline-flex;
                            width: 100%;
                            flex-direction: column;
                            background-color: #393939;
                            opacity: 0;
                            visibility: hidden;
                            transition: .30s;
                            box-shadow: 0px 3px 30px -1px rgba(0,0,0.52);
                          li{
                            padding: 5px 20px;
                            cursor: pointer;
                            transition: .30s;

                            &:hover {
                              background-color:#292929;
                            }
                           }
                        }

                        &:hover .dropdown{
                            opacity: 1;
                            visibility: visible;
                        }

                    }
                }
            }
        }
    }
    .menu_lateral{
        position: absolute;
        width: 250px;
        height: 100vh;
        min-height: 600px;
        left: -270px;
        top: 0;
        background-color: black;
        box-shadow: 0px 3px 30px -1px rgba(0,0,0.52);
        padding-top: 20px;
        transition: .30s;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: space-between;


        @include desktop{
            left: 0px;
        }
        &.active{
            left: 0px;
            .close{
                width: calc(100vw - 250px);
            }
        }
        .top{
                img{
                    margin: 0 auto;
                    display: block;
                }
            hr{
                margin: 20px 0;
                color: #fff;
            }
            ul{
                text-align: left;
            li{
               
                transition: .30s;

                a{
                    padding: 5px 10px;
                    display: block;
                    width: 100%;

                    svg{
                        font-size: 20px;
                        margin-right: 10px;
                          }
                     }
                     &.nome_categoria{
                        color: #bebebe;
                        border-bottom: 1px solid #bebebe;
                        margin-bottom: 5px;
                        padding: 5px 10px;
                        cursor: default;
                        &:hover{
                            background-color: transparent;
                        }
                    }
                    &:hover{
                        background-color: #3b3a3a;
                    }
                    &.active{
                        background-color: #3b3a3a;
                    }
                }
            }
        }

        .bottom{
            padding: 10px 10px;
            font-size: 12px;
        }

        .close{
            position: relative;
            width: 0px;
            top: 0;
            left: 0px;
            height: 100vh;
            background-color: #0006;
            text-align: right;
            overflow: hidden;
            svg{
                font-size: 35px;
                padding: 5px 15px;
                width: 50px;
                height: 50px;
                cursor: pointer;
                transition: .30s;
                   &:hover{
                    color: black;
                }
            }
            
            @include desktop{
                display: none;

            }
        }
    }
}


