footer{
	.footer-top{
        padding: 80px 0px;
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: space-between;

        
            .logo{
            display: inline-flex;
            width: 100%;
            flex-wrap: wrap;
            justify-content: center;
            margin-bottom: 40px;

            .img{
                margin-bottom: 35px;
            }

            .descricao{
                text-align: center;
                margin-bottom: 35px;
            }

            .redes-social{
                    width: 100%;
                    text-align: center;
                    margin-bottom: 10px;
                    a{
                        margin-right: 15px;
                        font-size: 17px;
                        &:last-child{
                            margin-right: 0px;
                    }
                    &:hover{
                        color:$color_destaque_hover;
                    }
                }
            }
        }

        .menu-institucional,
        .menu-servicos,
        .contatos{
        
            width: 100%;
            margin-bottom: 40px;
            .titulo{
                font-size: 24px;
                font-family: 'Montserrat Bold';
                margin-bottom: 35px;
                position: relative;

                &:after{
                    content: ' ';
                    height: 5px;
                    width: 100px;
                    background-color: $color_destaque;
                    position: absolute;
                    bottom: -9px;
                    left: 0px;

                }
            }
        }
        .contatos{
            margin-bottom: 0px;
            .informacoes{
                .endereco,
                .telefone,
                .email{
                    display: inline-flex;
                    justify-content: flex-start;
                    flex-wrap: wrap;
                    width: 100%;
                    margin-bottom: 65px;
                    .icon{
                        width: 28px;
                        text-align: center;
                        svg{
                            font-size: 30px;
                        }
                    }
                    p{
                        margin-left: 15px;
                        width: calc(100% - 43px);

                        strong{
                            display: block;
                            font-family: 'Montserrat Bold';
                        }
                        a{
                            transition: .20s;
                            &:hover{
                                color:blue;
                            }
                        }
                    }
                }

                .email{
                    margin-bottom: 0px;
                }

            }
        }

        .menu-institucional,
        .menu-servicos{
            ul{
                li{
                    margin-bottom: 20px;
                    a{
                        font-size: 16px;
                        font-family: 'Montserrat Medium';
                        display: block;
                        transition: .20s;
                        svg{
                            margin-right: 15px;
                        }
                        &:hover{
                            color: blue;
                        }
                    }
                    &:last-child{
                        margin-bottom: 0px;
                    }
                }
            }
         }
        
        @include tablet{
            .logo,
            .menu-institucional,
            .menu-servicos,
            .contatos{
                width: 24%;
            }

            .logo{
                justify-content: flex-start;
            .descricao,
            .redes-social{
                    text-align: left;
                
              }
            }
        }  
    }

    .footer-bottom{
        width: 100%;
        display: inline-flex;
        flex-wrap: wrap;
        border-top: 5px dashed $color_destaque;
        padding: 35px 0px;

        .container{
            display: inline-flex;
            flex-wrap: wrap;
            justify-content: center;

            .copy,
            .dev{
                width: 100%;
                text-align: center;
            }
            .dev{
                a{
                    transition: .20s;
                    &:hover{
                        color:blue;
                    }
                }
            }
            @include tablet{
                justify-content: space-between;

                .copy,
                .dev{
                  width: auto;
                  max-width:50%;
                  text-align: initial;
            }
            
        }

    }

   }  
 }


  